  $time: -4px -4px 30px 30px 38px 147px './test.svg' 'time';
  $certificate: -4px -38px 28px 29px 38px 147px './test.svg' 'certificate';
  $slide-arrow: -4px -71px 9px 17px 38px 147px './test.svg' 'slide-arrow';
  $star: -4px -92px 15px 16px 38px 147px './test.svg' 'star';
  $enable-check: -4px -112px 16px 13px 38px 147px './test.svg' 'enable-check';
  $disable-check: -4px -129px 14px 14px 38px 147px './test.svg' 'disable-check';



@mixin svg-sprite-position($svg-sprite) {
  $svg-sprite-offset-x: nth($svg-sprite, 1);
  $svg-sprite-offset-y: nth($svg-sprite, 2);
  background-position: $svg-sprite-offset-x  $svg-sprite-offset-y;
}

@mixin bg-svg($svg-sprite, $repeat: no-repeat) {
    background-image: url('%=static=%svg-sprite/sprite.svg');
    @include svg-sprite-position($svg-sprite);
    width: nth($svg-sprite, 3);
    height: nth($svg-sprite, 4);
    background-repeat: $repeat;
}
